import string from './css.js' //æ¨¡å—åŒ–

const player = {
    id: undefined,
    n: 1,
    time: 100,
    ui: {
        demo1: document.querySelector('#demo1'),
        demo2: document.querySelector('#demo2')
    },
    events: {
        '#btnStop': 'stop',             //æ¸…æ‰
        '#btnPlay': 'play',
        '#btnSlow': 'slow',
        '#btnMid': 'normal',
        '#btnFast': 'fast',
    },
    init: () => {                                              //åˆå§‹åŒ–
        player.ui.demo1.innerText = string.substr(0, player.n)
        player.ui.demo2.innerHTML = string.substr(0, player.n)   //åŒæ—¶ä»¥æ–‡æœ¬çš„å½¢å¼å’Œhtmlçš„å½¢å¼å±•ç¤ºæ ‡ç­¾

        player.bindEvents()
        player.play()
    },
    bindEvents: () => {
        for (let key in player.events) {
            const value = player.events[key]
            document.querySelector(key).onclick = player[value]  //ç”¨å­—ç¬¦ä¸²å–æ–¹æ³•
        }
    },
    run: () => {
        player.n += 1;
        if (player.n > string.length) {
            window.clearInterval(player.id)//å–æ¶ˆå…ˆå‰è®¾å®šçš„setIntervalä»»åŠ¡
            return
        }
        player.ui.demo1.innerText = string.substr(0, player.n)
        player.ui.demo2.innerHTML = string.substr(0, player.n)
        player.ui.demo1.scrollTop = player.ui.demo1.scrollHeight               //è‡ªåŠ¨æ»šåŠ¨ã€‚åˆ°ä»£ç çš„é«˜åº¦
    },
    stop: () => {
        window.clearInterval(player.id)
    },
    play: () => {
        player.id = setInterval(player.run, player.time)              //æ¯æ¬¡è°ƒç”¨playçš„æ—¶å€™éƒ½ä¼šæŠŠå®ƒèµ‹å€¼ç»™id
        //è€ƒè™‘å°†å®ƒå†…ç½®
    },
    slow: () => {
        player.stop()                //æŠŠé—¹é’Ÿç ¸äº†ï¼Œå†é‡æ–°å®šé—¹é’Ÿ
        player.time = 200
        player.play()
    },
    normal: () => {
        player.stop()
        player.time = 100
        player.play()
    },
    fast: () => {
        player.stop()
        player.time = 0
        player.play()
    },
}

player.init()


